/* A Modern CSS Reset */
*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

ul[role=list],
ol[role=list] {
  list-style: none;
}

html:focus-within {
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img,
picture {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
html {
  font-size: 16px;
}
@media (min-width: 375px) {
  html {
    font-size: 10px;
    font-size: 2.6666666667vw;
  }
}
@media (min-width: 720px) {
  html {
    font-size: 10px;
    font-size: 0.78125vw;
  }
}

body {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1;
  color: #000;
}

.container {
  width: 100%;
}

.content {
  width: 90%;
  margin: 0 auto;
  color: #fff;
}

.header {
  padding: 2.4rem;
  background-color: rgb(1, 145, 104);
}

.footer {
  height: 30vh;
  padding: 2.4rem;
  background-color: rgb(1, 145, 104);
}

.fv {
  background-image: url("../../assets/images/sample-bg.webp");
}
.fv .content {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 500px;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  color: #000;
}